* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  body {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: aquamarine;
  }
  
  .container {
    width: 300px;
    height: 350px;
    display: flex;
  
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: white;
  }
  
  h1 {
    background-color: rgb(12, 12, 108);
    width: 100%;
    text-align: center;
    color: white;
  }
  
  button {
    margin-top: 30px;
    margin-right: 25px;
    width: 100px;
    height: 40px;
    font-size: 18px;
    font-weight: 600;
    border: none;
    background-color: rgb(7, 7, 156);
    color: white;
  }
  button:hover {
    background-color: rgb(114, 114, 180);
  }
  